<!-- by 添少 -->
<template>
  <fixed-layout>
    <ui-article>
      <template slot="title">{{title||'马尔代夫'}}</template>
      <template slot="summary">{{summary||'这是一个字数刚刚好的摘要，简明扼要介绍了该介绍的一切。'}}</template>
      {{content||'轿车开进增江，一男子溺亡，建议自驾车配备安全锤南都讯 每次安全事故，都是一个惨痛的教训。7月19日，一辆深圳牌照的轿车开进了龙门县增江河，人车溺水后，司机有时间报警但没机会自救，结果溺死。如果车内备有一把应急安全锤，或许可以击碎车窗玻璃逃生自救。'}}
    </ui-article>
    <template slot="footer">
      <section class="bar">
        <button class="primary" style="flex-basis:30%;" @click="handSubmit">发布行程</button>
        <button @click="handSave">存草稿</button>
      </section>
    </template>
  </fixed-layout>
</template>
<script>
  import { mapState } from 'vuex'
  import FixedLayout from '@/components/fixed-layout'
  import UiArticle from '@/components/ui-article'

  export default {
    name: '',
    components: {
      FixedLayout,
      UiArticle
    },
    props: {},
    data () {
      return {}
    },
    watch: {},
    computed: {
      ...mapState({
        cover: state => state.write.article.cover,
        title: state => state.write.article.title,
        summary: state => state.write.article.summary,
        content: state => state.write.article.content,
      })
    },
    created () {
      this.init()
    },
    mounted () {
    },
    methods: {
      init () {
      },
      initData () {},
      handSubmit () {

      },
      handSave () {}
    }
  }
</script>
<style lang="scss">

</style>
<style lang="scss" scoped>
  .fixed-layout {
    background-color: #fff;
  }

  .article {
    padding: 0 15px;
  }

  .bar {
    background-color: #fff;
    border-top: solid #1797c6 1px;
    display: flex;
    button {
      background-color: #fff;
      font-size: 18px;
      color: #38a6ce;
      padding: 10px 0;
      border: none;
      flex: 1;
      &.primary {
        background: linear-gradient(to bottom, #4cafd3, #1797c6);
        color: #fff;
      }
    }
  }

</style>



